@import url('https://fonts.googleapis.com/css2?family=Signika+Negative:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Signika Negative';
    scroll-behavior: smooth;
  }

section {
    display: flex;
    flex-wrap: wrap;
    min-height: 100vh;
}

#inicio {
    display: flex;
    min-height: 94vh;
    justify-content: space-evenly;
}

.imagem::before {
    content: "";
    position: absolute;
    top:5%;
    background-color: #008F8C;
    width: 100%;
    height: 80%;
    z-index: 0;
}

.fotoInicial {
    position: absolute;
    top: 10%;
    height: 90%;
    bottom: 0;
}

nav {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    padding-right: 50px;
    font-size: 1.2em;
    font-weight: 800;
    box-shadow: 0px 1px 4px rgb(0,0,0, 0.25);
}

nav a {
    padding-right: 50px;
    text-decoration: none;
    color: #015958;
}

h1 {
    color: #015958;
    text-align: center;
    margin: 15px;
}

.apresentacao {
    display: flex;
    flex-direction: column;
    width: 35%;
    margin-top: 8%;
}

span {
    padding-left: 30px;
    font-size: 1.1em;
}

.imagem {
    display: flex;
    justify-content: center;
    width: 40%;
    position: relative;
}

#sobreMim {
    justify-content: space-evenly;
    background-color: rgb(243, 241, 241);
    align-items: center
}

.textoSobreMim, .habilidades {
    min-height: 70vh;
    background-color: white;
    box-shadow: 1rem 20px 4px #008F8C;
    padding: 3%;
}

.textoSobreMim {
    width: 50%;
    text-align: justify;
    font-size: 1.1em;
    line-height: 1.5;
}


.habilidades {
    width: 30%;
    display: flex;
    flex-direction: column;
    min-width: 300px;
    justify-content: space-evenly;
    align-items: center;
}

.habilidades li {
    font-size: 1.1em;
    padding: 5px;
    list-style: none;
    text-align: center;
}

.habilidadeTecnica{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

h3 {
    background-color: #008F8C;
}

#projetos {
    flex-direction: column;
}

.projetos {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.imgProjeto {
    width: 100%;
}

.projeto {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25%;
    background-color: #015958;
    margin: 10px;
    margin-left: 4%;
    margin-right: 4%;
}

.projeto a {
    text-decoration: none;
    color: white;
}

.projeto p,.projeto h4{
    color: white;
}

h4 {
    margin: 2px;
}

footer {
    height: auto;
    width: 100%;
    box-shadow: 1rem -1px 4px rgb(0,0,0, 0.25);
    margin: 0;
    padding: 5px;
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: flex-end;
}

footer a {
    padding-right: 10px;
}

@media screen and (max-width: 730px){
   #inicio {
    height: auto;
   }
   
    .imagem, .apresentacao {
    width: 90%;
   }

   .fotoInicial{
    width: 80%;
    position: inherit;
   }

   .imagem::before{
    top:5px;
    width: 80%;
   }

   .apresentacao{
    height: 150px;
   }

   nav{
    padding-right: 0;
   }

   nav a {
    padding-right: 20px;
   }

   body{
    font-size: 0.8em;
   }

   #sobreMim {
    flex-direction: column;
   }

   .habilidades, .textoSobreMim{
    width: 80%;
    margin: 20px;
    min-height: auto;
   }
   .projeto{
    width: 80%;
   }

   .projetos{
    justify-content: center;
   }
}

@media screen and (min-width: 1300px){
    body{
        font-size: 25px;
    }
    .habilidadeTecnica a img{
        width: 100%;
        height: 50px;
    }
}